import CreateSlideForm from '@/components/CreateSlideForm.vue';
export default Vue.extend({
  name: 'Modal',
  components: {
    SignupForm,
    SigninForm,
    CreateSlideForm,
  },
  computed: {
    ...mapState(['modalType']),
  },
  methods: {
    ...mapMutations({closeModal: VuexMutation.CLOSE_MODAL}),
  },
